import React from 'react'
import ReactDOM from 'react-dom'
import './App.css';
import { useState } from 'react';

function App() {
  const [newTask, setNewTask] = useState("");
  const [toDoList, setToDoList] = useState([]);

  const addTask = () => {
    const newTaskAdd = {
      id: toDoList.length === 0 ? 1 : toDoList[toDoList.length - 1].id + 1,
      taskName: newTask,
      completed: false,

    }
    setToDoList([...toDoList, newTaskAdd]);
  }
  const newTaskAdd = (event) => {
    setNewTask(event.target.value);
  }
  const deleteTask = (id) => {
    setToDoList(toDoList.filter((task) => task.id !== id));

  }
  const done = (id) => {
    setToDoList(toDoList.map((task) => {
      if (task.id === id) {
        return { ...task, completed: true };

      }
      else {
        return { task };
      }
    }
    ))
  }
  return (
    <div className='App'  >
      <h1 className='heading' >To-Do List</h1>
      <div className='inputBox' >
        <input className='input' onChange={newTaskAdd} placeholder="Add your tasks" />
        <button className='addTaskButton' onClick={addTask} >+</button>
      </div>
      <div>
        {toDoList.map((task) => {

return (
            <div className='show' style={{ backgroundColor: task.completed === true ? "green" : "white" }} >
              <table>
                <tr>
                  <td><h2>{task.taskName}</h2></td>
                  <td><button className='deleteButton' onClick={() => deleteTask(task.id)}>Remove</button>
                  </td>
                  <td><button className='done' onClick={() => done(task.id)}  >Done</button></td>
                </tr>
              </table>


            </div>);

        })
        }

      </div>

    </div>
  );

}

export default App;
